.form{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.form>label {
    text-align: center;
    margin: 10px auto;
    display: flex;
    flex-direction: column;
}

input{
    width:250px;
    padding:3px 5px;
    margin:10px 10px;
    font-size: 17px;
    border:1.5px solid black;
    outline: none;
}

.addTaskBtn{
    font-size: 20px;
    margin: 50px;
    padding:8px 30px;
    cursor: pointer;
}