.App{
    padding:10px;
    background-color: #777;
    max-width: 500px;
    margin:0 auto;
    min-height: 100vh;
    box-shadow: 0px 0px 15px 2px #000;
}

body {
    text-align: center;
    font-family: Helvetica, sans-serif;
    box-sizing: border-box;
    color:white;
    text-shadow: 1px 1px black;
}

button{
    background-color: #fff;
    border:1.25px solid black;
    margin: 0px 2px;
    padding: 4px 14px;
    font-size: 12px;
    transition: .2s linear;
    letter-spacing: 2px;
    cursor: pointer;
}

button:hover{
    background-color: rgba(0,0,0,0.9);
    color:white;
    border-color:white
}

.spanColor {
    color:darkcyan
}

.important{
    color:firebrick
}

strong {
    font-size: 22px;
    letter-spacing: 1px;
    margin-right:5px
}

.listItem{
    position: relative;
    left:0;
    display: flex;
    flex-direction: column;
    opacity: 0;
    max-width: 200px;
    word-wrap: break-word;
    margin: 15px auto;
    transition: 0.5s linear;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
}

.showTask{
    opacity: 1;
}

.dateSpan{
    display: block;
    margin: 10px 0px;
}

::-webkit-scrollbar{
    width: 4px;
}
::-webkit-scrollbar-track{
    background:black;
}
::-webkit-scrollbar-thumb{
    background: #eee;
}
::-webkit-scrollbar-thumb:hover{
    background: linear-gradient(180deg, rgb(122, 255, 240) 0%, rgb(21, 91, 83) 100%);
}